/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

body,
button,
input,
select,
textarea {
  color: $color__text-main;
  font-family: $font__main;

  @include font-size(1);

  line-height: $font__line-height-body;
}

/* Headings */

h1, h2, h3, h4, h5, h6 {
  clear: both;
  font-family: 'novocento', Arial, Serif;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

.entry-header {
  font-size: 1.25rem;
}

.contact-form-header {
  font-size: 2rem;
  color: $color__purple-brand;
}

/* Copy */

.entry-content {
  line-height: 1.8rem;
}

p {
  margin-bottom: $base__space-unit;
}

dfn, cite, em, i {
  font-style: italic;
}

i.fa {
  margin-right: 0.5rem;
}

.arrow {
  position: relative;
  bottom: 2px;
}

.read-more-button .arrow {
  margin-left: 0.25rem;
}

blockquote {
  margin: 0 $base__space-unit;
}

address {
  margin: 0 0 $base__space-unit;
}

pre {
  background: $color__background-pre;
  font-family: $font__pre;

  @include font-size(0.9375);

  line-height: $font__line-height-pre;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem;
}

code, kbd, tt, var {
  font-family: $font__code;

  @include font-size(0.9375);
}

abbr, acronym {
  border-bottom: 1px dotted $color__border-abbr;
  cursor: help;
}

mark, ins {
  background: $color__background-ins;
  text-decoration: none;
}

big {
  font-size: 125%;
}